.Posts {
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    width: 100%;
    height: 400px;
    margin: auto;
    overflow: scroll;
    background: -webkit-gradient(linear,left bottom, left top,from(darkgrey), to(rgb(221, 221, 221)));
    background: -webkit-linear-gradient(bottom,darkgrey, rgb(221, 221, 221));
    background: -o-linear-gradient(bottom,darkgrey, rgb(221, 221, 221));
    background: linear-gradient(0deg,darkgrey, rgb(221, 221, 221));
}

.Container {
    background-color: rgb(220, 238, 245);
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    margin: auto;
}

.Container input{
    max-width: 400px;
    width: 80%;
    margin: auto;
    margin-top:-16px;
    margin-bottom:16px;
    background: -webkit-gradient(linear, left top, right top, from(rgb(0, 255, 0)), to(red));
    background: -webkit-linear-gradient(left, rgb(0, 255, 0), red);
    background: -o-linear-gradient(left, rgb(0, 255, 0), red);
    background: linear-gradient(90deg, rgb(0, 255, 0), red);
    border-radius: 10px;
}


