.Post{
    width: 400px;
    padding: 4px 16px;
    margin: 8px;
    min-height: 100px;
    overflow-y: auto;
    text-align: center;
    font-size: 8px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: white;
    border: 1px solid rgb(88, 88, 207);
    -webkit-box-shadow: 4px 4px 10px rgb(120, 120, 120);
            box-shadow: 4px 4px 10px rgb(120, 120, 120);
}

.Post:hover,
.Post:active {
    text-decoration: underline;
    border: 2px solid rgb(88, 88, 207);
    padding: 3px 15px; 
    -webkit-box-shadow: 4px 4px 10px rgb(80, 80, 80); 
            box-shadow: 4px 4px 10px rgb(80, 80, 80);
}

.Author {
    font-size: 8px;
    color: grey;
}


